/* --------------------------
 DFLEX
---------------------------*/
@mixin u-flex($jc: flex-start, $ai: flex-start, $fd: '', $fw: row wrap) {
    display: flex;
    justify-content: $jc;
    align-items: $ai;
    flex-flow: $fw;
    
    @if $fd != '' {
        flex-direction: $fd;
    }
}